@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

.container {
  max-width: 1170px;
  margin: auto;

}

.row {
  display: flex;
  flex-wrap: wrap;
}

ul {
  list-style: none;
  padding: 0 !important;
}

.paddingzero {
  margin-left: -40px;
}

.footer {
  margin-top: 80px;
  background-color: #ffffff;
  padding: 70px 0;
}

.footer-col {
  width: 25%;
  padding: 0 15px;
}

.footer-col h4 {
  font-size: 18px;
  color: #000000;
  text-transform: capitalize;
  margin-bottom: 35px;
  font-weight: 500;
  position: relative;
}

.footer-col h4::before {
  content: '';
  position: absolute;
  left: 0;
  bottom: -10px;
  background-color: orangered;
  height: 2px;
  box-sizing: border-box;
  width: 50px;
}

.footer-col ul li:not(:last-child) {
  margin-bottom: 10px;
}

.footer-col ul li a {
  font-size: 16px;
  text-transform: capitalize;
  color: #000000;
  text-decoration: none;
  font-weight: 300;
  color: #00000072;
  display: block;
  transition: all 0.3s ease;
}

.footer-col ul li a:hover {
  color: #000000;
  padding-left: 8px;
  text-decoration: underline;
}

.footer-col .social-links a {
  display: inline-block;
  height: 20px;
  width: 40px;
  background-color: rgba(255, 255, 255, 0.2);
  margin: 0 10px 10px 0;
  text-align: center;
  line-height: 40px;
  border-radius: 50%;
  color: #000000;
  transition: all 0.5s ease;
}

.footer-col .social-links a:hover {
  color: #fff;
  background-color: orange;

}

/*responsive*/
@media(max-width: 767px) {
  .footer-col {
    width: 50%;
    margin-bottom: 30px;
  }
}

@media(max-width: 425px) {
  .footer-col {
    width: 100%;
  }

  .footer {
    padding: 0;
  }
}



.phonenumber h2 {
  color: #000000;
  font-size: 22px;
}


.tg-silka a {
  font-size: 20px;
  color: #fff;
}

.default-li {
  display: flex;
  text-transform: capitalize;
  align-items: center;
  gap: 8px;
}

.default-li i {
  padding: 8px;
  background-color: rgba(255, 255, 255, 0.2);
  font-size: 11px;
  margin: 0 10px 10px 0;
  text-align: center;
  border-radius: 50%;
  color: #ffffff;
  transition: all 0.5s ease;
}

.email-adress a {
  text-transform: lowercase;
}